import PropTypes from 'prop-types';
import { useState } from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { Box, IconButton, Tooltip, Stack, Chip } from '@mui/material';
import { useSnackbar } from 'notistack';
import Iconify from '../../components/Iconify';

// ----------------------------------------------------------------------

CodeBlock.propTypes = {
    code: PropTypes.string.isRequired,
    language: PropTypes.string,
    title: PropTypes.string,
    showLineNumbers: PropTypes.bool,
};

export default function CodeBlock({ code, language = 'javascript', title, showLineNumbers = true }) {
    const { enqueueSnackbar } = useSnackbar();
    const [copied, setCopied] = useState(false);

    const handleCopy = () => {
        setCopied(true);
        enqueueSnackbar('Code copied to clipboard!', { variant: 'success' });
        setTimeout(() => setCopied(false), 2000);
    };

    return (
        <Box sx={{ position: 'relative', my: 2 }}>
            {title && (
                <Stack
                    direction="row"
                    alignItems="center"
                    justifyContent="space-between"
                    sx={{
                        bgcolor: 'grey.900',
                        px: 2,
                        py: 1,
                        borderTopLeftRadius: 1,
                        borderTopRightRadius: 1,
                    }}
                >
                    <Chip label={language.toUpperCase()} size="small" color="primary" />
                    <CopyToClipboard text={code} onCopy={handleCopy}>
                        <Tooltip title={copied ? 'Copied!' : 'Copy code'}>
                            <IconButton size="small" sx={{ color: 'common.white' }}>
                                <Iconify icon={copied ? 'eva:checkmark-fill' : 'eva:copy-fill'} />
                            </IconButton>
                        </Tooltip>
                    </CopyToClipboard>
                </Stack>
            )}

            <Box sx={{ position: 'relative' }}>
                {!title && (
                    <Box sx={{ position: 'absolute', top: 8, right: 8, zIndex: 1 }}>
                        <CopyToClipboard text={code} onCopy={handleCopy}>
                            <Tooltip title={copied ? 'Copied!' : 'Copy code'}>
                                <IconButton
                                    size="small"
                                    sx={{
                                        bgcolor: 'rgba(255,255,255,0.1)',
                                        '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' },
                                        color: 'common.white',
                                    }}
                                >
                                    <Iconify icon={copied ? 'eva:checkmark-fill' : 'eva:copy-fill'} />
                                </IconButton>
                            </Tooltip>
                        </CopyToClipboard>
                    </Box>
                )}

                <SyntaxHighlighter
                    language={language}
                    style={vscDarkPlus}
                    showLineNumbers={showLineNumbers}
                    customStyle={{
                        margin: 0,
                        borderRadius: title ? '0 0 8px 8px' : '8px',
                        fontSize: '14px',
                    }}
                >
                    {code}
                </SyntaxHighlighter>
            </Box>
        </Box>
    );
}
