// @mui
import { Container, Typography, Card, Box, Stack, Alert, Chip } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
import useAuth from '../../hooks/useAuth';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { CodeBlock, EndpointCard, ParameterTable } from '../../sections/docs';
import { HOST_API } from '../../config';

// ----------------------------------------------------------------------

export default function DocsExam() {
  const { themeStretch } = useSettings();
  const { setting, user } = useAuth();

  const requestCode = `$ch = curl_init();
curl_setopt($ch, CURLOPT_URL, "${HOST_API}/api/epin");
curl_setopt($ch, CURLOPT_POST, 1);
curl_setopt($ch, CURLOPT_POSTFIELDS, http_build_query([
    'exam_name' => 1,
    'quantity' => 1
]));
curl_setopt($ch, CURLOPT_RETURNTRANSFER, 1);
curl_setopt(
    $ch, CURLOPT_HTTPHEADER, [
        "Authorization: Bearer {$user?.apikey}",
        "Content-Type: application/x-www-form-urlencoded"
    ]
);
$response = curl_exec($ch);
curl_close($ch);`;

  const successResponse = `{
    "status": "success",
    "message": "Exam PIN purchase successful",
    "data": {
        "exam_name": "WAEC",
        "quantity": 1,
        "pins": [
            {
                "serial": "123456789012",
                "pin": "1234567890",
                "username": "habukhan"
            }
        ],
        "amount": "3500.00",
        "balance_before": "10000.00",
        "balance_after": "6500.00",
        "reference": "habukhan-dev-${Date.now()}"
    }
}`;

  const parameters = [
    {
      name: 'exam_name',
      type: 'integer',
      required: true,
      description: 'Exam board ID (see exam IDs reference)',
    },
    {
      name: 'quantity',
      type: 'integer',
      required: true,
      description: 'Number of PINs to purchase (1-10)',
    },
  ];

  return (
    <Page title="Exam Result Checker API">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Exam Result Checker API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Documentation', href: '/documentation/home' },
            { name: 'Exam' },
          ]}
        />

        {/* Overview */}
        <Card sx={{ p: 4, mb: 3 }}>
          <Stack direction="row" alignItems="center" spacing={2} sx={{ mb: 2 }}>
            <Typography variant="h4">Exam Result Checker</Typography>
            <Chip label="POST" color="success" size="small" />
          </Stack>
          <Typography variant="body1" color="text.secondary">
            Purchase result checker PINs for WAEC, NECO, NABTEB and other exam boards. Instant PIN generation.
          </Typography>
        </Card>

        {/* Endpoint */}
        <EndpointCard
          method="POST"
          endpoint={`${HOST_API}/api/epin`}
          description="Purchase exam result checker PINs"
        >
          <ParameterTable parameters={parameters} title="Request Parameters" />

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Code Example (PHP)
            </Typography>
            <CodeBlock code={requestCode} language="php" title="PHP" />
          </Box>

          <Box sx={{ mt: 3 }}>
            <Typography variant="subtitle2" gutterBottom>
              Success Response
            </Typography>
            <CodeBlock code={successResponse} language="json" title="JSON Response" />
          </Box>
        </EndpointCard>

        {/* Important Notes */}
        <Card sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Important Notes
          </Typography>
          <Stack spacing={2}>
            <Alert severity="info">
              <Typography variant="body2">
                <strong>Supported Exams:</strong> WAEC, NECO, NABTEB
              </Typography>
            </Alert>
            <Alert severity="warning">
              <Typography variant="body2">
                <strong>Quantity Limit:</strong> Maximum 10 PINs per transaction
              </Typography>
            </Alert>
            <Alert severity="success">
              <Typography variant="body2">
                <strong>Instant Generation:</strong> PINs are generated and delivered instantly.
              </Typography>
            </Alert>
          </Stack>
        </Card>

        {/* See Also */}
        <Card sx={{ p: 3 }}>
          <Typography variant="h6" gutterBottom>
            See Also
          </Typography>
          <Stack spacing={1}>
            <Typography variant="body2">
              â€¢ <a href="/documentation/examid">Exam Board IDs Reference</a>
            </Typography>
          </Stack>
        </Card>
      </Container>
    </Page>
  );
}
