import PropTypes from 'prop-types';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Chip,
    Typography,
    Box,
} from '@mui/material';

// ----------------------------------------------------------------------

ParameterTable.propTypes = {
    parameters: PropTypes.arrayOf(
        PropTypes.shape({
            name: PropTypes.string.isRequired,
            type: PropTypes.string.isRequired,
            required: PropTypes.bool,
            description: PropTypes.string,
        })
    ).isRequired,
    title: PropTypes.string,
};

export default function ParameterTable({ parameters, title = 'Parameters' }) {
    return (
        <Box sx={{ my: 3 }}>
            <Typography variant="h6" sx={{ mb: 2 }}>
                {title}
            </Typography>
            <TableContainer component={Paper} variant="outlined">
                <Table>
                    <TableHead>
                        <TableRow sx={{ bgcolor: 'grey.100' }}>
                            <TableCell sx={{ fontWeight: 'bold' }}>Parameter</TableCell>
                            <TableCell sx={{ fontWeight: 'bold' }}>Type</TableCell>
                            <TableCell sx={{ fontWeight: 'bold' }}>Required</TableCell>
                            <TableCell sx={{ fontWeight: 'bold' }}>Description</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {parameters.map((param, index) => (
                            <TableRow key={index} hover>
                                <TableCell>
                                    <Typography
                                        variant="body2"
                                        sx={{ fontFamily: 'monospace', fontWeight: 'medium' }}
                                    >
                                        {param.name}
                                    </Typography>
                                </TableCell>
                                <TableCell>
                                    <Chip label={param.type} size="small" variant="outlined" />
                                </TableCell>
                                <TableCell>
                                    <Chip
                                        label={param.required ? 'Yes' : 'No'}
                                        size="small"
                                        color={param.required ? 'error' : 'default'}
                                        variant={param.required ? 'filled' : 'outlined'}
                                    />
                                </TableCell>
                                <TableCell>
                                    <Typography variant="body2" color="text.secondary">
                                        {param.description}
                                    </Typography>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Box>
    );
}
