import PropTypes from 'prop-types';
import { Card, Stack, Typography, Chip, Box, alpha } from '@mui/material';
import Iconify from '../../components/Iconify';

// ----------------------------------------------------------------------

EndpointCard.propTypes = {
    method: PropTypes.oneOf(['GET', 'POST', 'PUT', 'DELETE']).isRequired,
    endpoint: PropTypes.string.isRequired,
    description: PropTypes.string,
    children: PropTypes.node,
};

export default function EndpointCard({ method, endpoint, description, children }) {
    const getMethodColor = (method) => {
        const colors = {
            GET: 'info',
            POST: 'success',
            PUT: 'warning',
            DELETE: 'error',
        };
        return colors[method] || 'default';
    };

    return (
        <Card sx={{ p: 3, mb: 3 }}>
            <Stack spacing={2}>
                {/* Method and Endpoint */}
                <Stack direction="row" alignItems="center" spacing={2} flexWrap="wrap">
                    <Chip
                        label={method}
                        color={getMethodColor(method)}
                        size="small"
                        sx={{ fontWeight: 'bold', minWidth: 60 }}
                    />
                    <Box
                        sx={{
                            flex: 1,
                            bgcolor: (theme) => alpha(theme.palette.grey[500], 0.12),
                            px: 2,
                            py: 1,
                            borderRadius: 1,
                            fontFamily: 'monospace',
                            fontSize: '14px',
                            overflow: 'auto',
                        }}
                    >
                        {endpoint}
                    </Box>
                </Stack>

                {/* Description */}
                {description && (
                    <Typography variant="body2" color="text.secondary">
                        {description}
                    </Typography>
                )}

                {/* Content */}
                {children && <Box sx={{ mt: 2 }}>{children}</Box>}
            </Stack>
        </Card>
    );
}
